const ko = require('knockout');
const locations = require('./data.js');
import '../css/main.css';

// Initialize the map
var map;
function initMap() {
    map = new google.maps.Map(document.getElementById('map'), {
        center: { lat: -34.397, lng: 150.644 },
        zoom: 8
    });
    ko.applyBindings(new AppViewModel());
}

// A constructor object to store information for each place
var Place = function(data) {
    let that = this;
    that.name = ko.observable(data.name);
    that.lat = ko.observable(data.lat);
    that.lng = ko.observable(data.lng);
    that.description = ko.observable(data.description);
    that.address = ko.observable(data.address);
}

// ViewModel
var AppViewModel = function() {
    let that = this;
    that.placeList = ko.observableArray([]);
    locations.forEach(function (placeItem) {
        that.placeList.push(new Place(placeItem));
    });
    console.log(placeList);
}

window.initMap = initMap;


